function createXMLHTTPRequest() {
   // 1.XMLHttpRequest
   // XMLHttpReuquest
   // IE
   var xmlHttpRequest;
   if (window.XMLHttpRequest) {
       // FireFoxMozillarOperaSafariIE7IE8
       /* eslint-disable no-undef */
      xmlHttpRequest = new XMLHttpRequest();
       // mozillarBUG
       if (xmlHttpRequest.overrideMimeType) {
           xmlHttpRequest.overrideMimeType('text/xml');
       }
   } else if (window.ActiveXObject) {
       // IE6IE5.5IE5
       // XMLHTTPRequestjs
       // 
       var activexName = ['MSXML2.XMLHTTP', 'Microsoft.XMLHTTP'];
       for (var i = 0; i < activexName.length; i++) {
           try {
               // 
               // 
               /* eslint-disable no-undef */
              xmlHttpRequest = new ActiveXObject(activexName[i]);
              if (xmlHttpRequest) {
                  break;
              }
           } catch (e) {
           }
       }
   }
   return xmlHttpRequest;
}

function get(url, success, error) {
  var req = createXMLHTTPRequest();
  if (req) {
      req.open('GET', url, true);
      req.onreadystatechange = function() {
          if (req.readyState === 4) {
              if (req.status === 200) {
                  if (typeof success === 'function') success(req.responseText);
              } else {
                  if (typeof error === 'function') error();
              }
          }
      };
      req.send(null);
  }
};

function post(url, param, success, error) {
    var req = createXMLHTTPRequest();
    if (req) {
        req.open('POST', url, true);
        req.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded; charset=utf8;');
        req.send(param);
        req.onreadystatechange = function() {
            if (req.readyState === 4) {
                if (req.status === 200) {
                    if (typeof success === 'function') success(req.responseText);
                } else {
                    if (typeof error === 'function') error();
                }
            }
        };
    }
};

module.exports = {get, post};



